document.addEventListener("DOMContentLoaded", () => {
	//? Includes
	//* // Variables
const isiPhone = (navigator.userAgent.match(/iPhone/i) != null);
const isiPad = (navigator.userAgent.match(/iPad/i) != null);
const isiPod = (navigator.userAgent.match(/iPod/i) != null);

const body = document.querySelector('.body');
const headerContainer = document.querySelector('.header__container');
const burgerBtn = document.querySelector('.burger');
const nav = document.querySelector('.nav');
const mediaMobile = window.matchMedia("(max-width: 767px)");
const header = document.querySelector('.header');
const hero = document.querySelector('.hero');
const swiperSimpleSelector = document.querySelector(".simple__swiper");
const swiperPlatformSelector = document.querySelector('.simple__swiper--platform');
let isSmoothScrollSupported = 'scrollBehavior' in document.documentElement.style;

;
	//* // scrollLockIOS

// Выключение скролла
let disableScroll = () => {
	let pagePosition = window.scrollY;
	document.querySelector('html').style.scrollBehavior = 'auto';
	document.body.classList.add('ios-lock');
	document.body.dataset.position = pagePosition;
	document.body.style.top = -pagePosition + 'px';
}

// Включение скролла
let enableScroll = () => {
	let pagePosition = parseInt(document.body.dataset.position, 10);
	document.body.style.top = 'auto';
	document.body.classList.remove('ios-lock');
	window.scroll({ top: pagePosition, left: 0 });
	document.querySelector('html').removeAttribute('style');
	document.body.removeAttribute('data-position');
};
	//* // Scroll to anchors
if (!isSmoothScrollSupported) {
	const smoothScroll = (targetEl, duration, header) => {
		const headerElHeight = document.querySelector(`${header}`).clientHeight;
		let target = document.querySelector(targetEl);
		let targetPosition = target.getBoundingClientRect().top - headerElHeight;
		let startPosition = window.pageYOffset;
		let startTime = null;

		const ease = function (t, b, c, d) {
			t /= d / 2;
			if (t < 1) return c / 2 * t * t + b;
			t--;
			return -c / 2 * (t * (t - 2) - 1) + b;
		};

		const animation = function (currentTime) {
			if (startTime === null) startTime = currentTime;
			const timeElapsed = currentTime - startTime;
			const run = ease(timeElapsed, startPosition, targetPosition, duration);
			window.scrollTo(0, run);
			if (timeElapsed < duration) requestAnimationFrame(animation);
		};
		requestAnimationFrame(animation);
	};

	const scrollTo = () => {
		const links = document.querySelectorAll('[data-scroll]');

		links.forEach(each => {
			each.addEventListener('click', function (e) {
				e.preventDefault();
				const currentTarget = this.getAttribute('href');
				setTimeout(() => {
					smoothScroll(currentTarget, 1000, '.header');
				}, 200);
			});
		});
	};
	scrollTo();
};
	//* // Burger Menu
let menuIsOpen = false;

if (mediaMobile.matches) {
	nav.setAttribute('aria-hidden', true);
}

const burgerMenuOpen = () => {
	burgerBtn.classList.add('burger--active');
	body.classList.add('lock');
	nav.classList.add('nav--active')
	burgerBtn.setAttribute('aria-expanded', true);
	nav.setAttribute('aria-hidden', false);
	if (isiPad || isiPhone || isiPod) {
		disableScroll();
	}
	menuIsOpen = true;
}

const burgerMenuClose = () => {
	burgerBtn.classList.remove('burger--active');
	body.classList.remove('lock');
	nav.classList.remove('nav--active')
	burgerBtn.setAttribute('aria-expanded', false);
	nav.setAttribute('aria-hidden', true);
	if (isiPad || isiPhone || isiPod) {
		enableScroll();
	}
	menuIsOpen = false;
}

const burgerMenuSwitch = () => {
	switch (menuIsOpen) {
		case false:
			burgerMenuOpen();
			break;
		case true:
			burgerMenuClose();
			break;
	}
}

headerContainer.addEventListener('click', (e) => {
	if (e.target.classList.contains('burger')) {
		burgerMenuSwitch();
	} else if (e.target.classList.contains('nav__link') && window.innerWidth < 768) {
		burgerMenuSwitch();
	}
})
;
	//* // Header Scroll
window.addEventListener('scroll', () => {
	headerFixed();
});
const headerFixed = () => {
	if (!mediaMobile.matches) {
		if (window.scrollY > 0) {
			header.classList.add('header--fixed');
		} else if (window.scrollY === 0) {
			header.classList.remove('header--fixed');
		}
	} else if (mediaMobile.matches && window.scrollY > 0) {
		header.classList.add('header--fixed');
	}
}
headerFixed();;
	//* // swiperMobile
let swiperSimpleMobileInit = () => {
	if (window.innerWidth <= 768 && swiperSimpleSelector.dataset.mobile === "false") {
		swiperSimple = new Swiper(swiperSimpleSelector, {
			containerModifierClass: "swiper",
			wrapperClass: "swiper__wrapper",
			slideClass: "swiper__slide",
			loop: true,
			spaceBetween: 15,
			pagination: {
				el: ".swiper__pagination",
				clickable: true,
				bulletElement: 'button',
				bulletClass: 'swiper-pagination-bullet btn-reset'
			},
			a11y: {
				paginationBulletMessage: 'Перейти к слайду {{index}}',
			},
			breakpoints: {
				300: {
					slidesPerView: 1.3,
				},
				450: {
					slidesPerView: 1.8,
				},
			},
		});
		swiperSimpleSelector.dataset.mobile = "true";
	}
	if (window.innerWidth > 768) {
		swiperSimpleSelector.dataset.mobile = "false";
		if (swiperSimpleSelector.classList.contains('swiperinitialized')) {
			swiperSimple.destroy();
		}
	}
};
swiperSimpleMobileInit();


let swiperPlatformMobileInit = () => {
	if (window.innerWidth <= 768 && swiperPlatformSelector.dataset.mobile === "false") {
		swiperPlatform = new Swiper(swiperPlatformSelector, {
			containerModifierClass: "swiper",
			wrapperClass: "swiper__wrapper",
			slideClass: "swiper__slide",
			loop: true,
			spaceBetween: 15,
			pagination: {
				el: ".swiper__pagination",
				clickable: true,
				bulletElement: 'button',
				bulletClass: 'swiper-pagination-bullet btn-reset'
			},
			a11y: {
				paginationBulletMessage: 'Перейти к слайду {{index}}',
			},
			breakpoints: {
				300: {
					slidesPerView: 1.3,
				},
				450: {
					slidesPerView: 1.8,
				},
			},
		});
		swiperPlatformSelector.dataset.mobile = "true";
	}
	if (window.innerWidth > 768) {
		swiperPlatformSelector.dataset.mobile = "false";
		if (swiperPlatformSelector.classList.contains('swiperinitialized')) {
			swiperPlatform.destroy();
		}
	}
};
swiperPlatformMobileInit();

window.addEventListener("resize", () => {
	swiperSimpleMobileInit();
	swiperPlatformMobileInit();
});;
	//* // Accordion
const accordionTriggers = document.querySelectorAll(".accordion__trigger");

const accordionTriggerDisable = (trigger) => {
	trigger.disabled = true;
	setTimeout(() => {
		trigger.disabled = false;
	}, 500);
}

const accordionOpen = (trigger, body) => {
	body.style.height = body.scrollHeight + 'px';
	if (body.style.height === "0px" || window.getComputedStyle(body).height === "0px") {
		trigger.closest('.accordion__item').classList.add("accordion__item--active");
		trigger.classList.add("accordion__trigger--active");
		trigger.setAttribute("aria-expanded", "true");
		body.setAttribute("aria-hidden", "false");
		body.classList.remove('accordion__body--hidden');
	}
}

const accordionClose = (trigger, body) => {
	if (body.style.height !== "0px" && window.getComputedStyle(body).height !== "0px") {
		trigger.closest('.accordion__item').classList.remove("accordion__item--active");
		trigger.classList.remove("accordion__trigger--active");
		trigger.setAttribute("aria-expanded", "false");
		body.setAttribute("aria-hidden", "true");
		body.style.height = body.scrollHeight + 'px';
		setTimeout(() => {
			body.style.height = "0";
		}, 0);
		setTimeout(() => {
			body.classList.add('accordion__body--hidden');
		}, 500);
	}
}

const setHeightOnTransitionEnd = (body) => {
	body.addEventListener("transitionend", () => {
		if (body.style.height !== "0px") {
			body.style.height = "auto";
		}
	})
}

const accordionToggle = (target) => {
	const trigger = target;
	const body = target.closest('.accordion__item').querySelector('.accordion__body');
	accordionTriggerDisable(trigger);
	setHeightOnTransitionEnd(body);
	accordionClose(trigger, body);
	accordionOpen(trigger, body);
}

accordionTriggers.forEach(trigger => {
	trigger.addEventListener('click', (e) => {
		let self = e.currentTarget;
		accordionToggle(self);
	})
})
// -- //
;
	//? -- //
});